}

.row {
  display: -webkit-flex;
  display: flex;
}

.grid {
  display: grid;
  margin: 10px;
  height: 93vw;
  width: 92vw;
  max-width: 350px;
  max-height: 350px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: .5px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.box {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 1px;
  padding: 2px;
}


.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100vw;
}

.sudoku-menu {
