  padding-right: 10px;
}

.scribble-numbers {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-gap: 1px;
  width: 80vw;
  max-width: 350px;
  max-height: 39px;
  height: calc(92vw/9);
  /* border: solid white 2px; */
}

.available-numbers {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-gap: 1px;
  width: 80vw;
  max-width: 350px;
  max-height: 39px;
  height: calc(92vw/9);
  /* border: solid rgb(104, 51, 51) 2px; */
}

