.cube {
  /* margin-bottom: 50px; */
  margin-top: 50px;
  /* transform: scale(1.3); */
  /* width: 200px;
  height: 200px; */
  width: 200px;
  height: 200px;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: translateZ(-100px);
          transform: translateZ(-100px);
  cursor: -webkit-grab;
  cursor: grab;
  /* transition: transform 1s; */
}

.cube__face {
  display: grid;
  height: 120px;
  width: 120px;
  display: grid;
  /* grid-gap: 2px; */
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.piece {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 8px;
  border: solid 2px rgb(32, 32, 32);
  border-radius: 5px;
}

.rubik-nav {
  margin-top: 80px;
}

.rubik-nav > button {
  cursor: pointer;
}

.solved-text {
  color: green;
}

.unsolved-text {
  color: tomato;
}


.yellow {
